<script>
export default {

  name: 'EditObjectables',
  props: {
    hotel: {
      type: Object,
      required: true,
      default: null,
    },
    token: {
      type: String,
      required: true,
      default: null,
    },
    room: {
      type: Object,
      default: () => {
        return {}
      }
    },
    customers: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {
      dialog: false,
      name: '',
      qnt: 0,
      customerId: 15,
      roomType: 0,
      priceList: {
        adl: {
          std: 0,
          sngl: 0,
          extra: 0,
        },
        chd: {
          age: 7,
          std: 0,
          extra: 0,
        },
        inf: {
          age: 2,
          std: 0,
          isFree: true,
        },
      },
      additionalCount: 1,
      additionalPrices: [
        [
          { name: 'Ребёнок', price: 0 },
          { name: 'Взрослый', price: 0 }
        ],
      ]
    };
  },
  computed: {
    attribute: function() {
      return 'attribute[' + this.room.id + ']'
    },
    extra: function() {
      return JSON.stringify({
        priceList: {
          ...this.priceList,
          // additionalPrices: this.additionalPrices,
        }
      })
    },
    customerTypes: function() {
      let result = []
      Object.keys(this.customers).map((key) => {
        if (key != '') {
          result.push({
            id: key,
            name: this.customers[key]
          })
        }
      })
      return result
    },
  },
  mounted() {
    this.parseInfo()
  },
  methods: {
    log() {
      console.log(this.extra)
      console.log(this.name)
    },
    incrementAdditional() {
      if (this.additionalCount < 10) {
        this.additionalCount += 1
        this.additionalPrices.push([
          { name: 'Ребёнок', price: 0 },
          { name: 'Взрослый', price: 0 }
        ])
      }
    },
    decrementAdditional() {
      if (this.additionalCount > 0) {
        this.additionalPrices[this.additionalCount - 1][0].price = 0
        this.additionalPrices[this.additionalCount - 1][1].price = 0
        this.additionalCount -= 1
        this.additionalPrices.pop()
      }
    },
    parseInfo() {
      this.name = this.room.name
      this.qnt = this.room.qnt
      this.priceList = JSON.parse(this.room.extra).priceList
      // this.additionalPrices = JSON.parse(this.room.extra).priceList.additionalPrices
      // this.additionalCount = this.additionalPrices.length
    },
    close() {
      this.parseInfo()
      this.dialog = false
    }
  },
  
};
</script>

